.servico_detalhe{
    padding: 80px 0px;
    .texto{
        width: 100%;
        display: block;

        img{
            
            max-width: 100%;
            margin: 0px auto 25px auto;
            display: block;
            @include desktop{
                float: right;
                margin-left: 25px;
            }
        }
        .titulo{
            &::after{
                left: 0;
                top: 100%;
            }
         }
         p{
             margin-bottom: 25px;
             &:last-child{
                margin-bottom: 0px;
             }
         }
    }
}
.galeria_fotos{
    padding: 80px 0;
    background-color: #292929;

    .descricao{
        max-width: 685px;
        margin: 0 auto;
        display: block;
        text-align: center;
        margin-bottom: 25px;
    }
    .itens, 
    .itens, .item,
    .itens, .item, .img,
    .itens, .item, .img a,
    .itens, .item, .img a img{
        width: 100%;
        display: block;
    }
    .itens, .item{
        margin: 0px 10px 28px 0px;
        @include desktop{
            margin: 0px 40px 28px 0;
        }
    }
    .itens, .item, .img a{
        position: relative;
        .hover{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: #07189987;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            opacity: 0;
            visibility: hidden;
            transition: .30s;
            svg{
                padding: 10px;
                background-color: #fff;
                color: $color_destaque;
                width: 40px;
                height: 40px;
            }
        }
        &:hover .hover{
            opacity: 1;
            visibility: visible;
        }
    }
    
    .itens{
        .slick-dots li button{
            padding: 0px;
            background: #fff;
            border-radius: 50%;
            border: 3px solid #000;
            &::before{
                opacity: 0;
            }
        }
        .slick-dots li.slick-active button{
            border: 3px solid #fff;
        }
    }
}
.formulario_orcamento{
    padding: 60px 0;
    .img{
        display: none;
    }
    .form{
        width: 100%;
        background-color: #292929;
        padding: 25px;
        h2{
            font-size: 24px;
            font-family: 'Montserrat Bold';
            margin-bottom: 25px;
        }
        p{
            margin-bottom: 20px;
        }
        form{
            width: 100%;
            display: inline-flex;
            justify-content: space-between;
            flex-wrap: wrap;

            input[type='text'],
            input[type='email']{
                width: 100%;
                padding: 11px 15px;
                background-color: #fff;
                color: #292929;
                border: 0px;
                margin-bottom: 20px;
                font-size: 16px;    
            }
            strong{
                font-family: 'Montserrat Bold';
                width: 100%;
                display: block;
            }
            label{
                width: 100%;
                margin-bottom: 15px;
                @include tablet{
                    width: 49%;
                }
                @include desktop{
                    width: 32%;
                }
            }
            textarea{
                font-family: 'Montserrat Regular';
                width: 100%;
                padding: 11px 15px;
                background-color: #fff;
                color: #292929;
                border: 0px;
                margin-bottom: 20px;
                font-size: 16px;
                height: 115px;
                min-width: 100%;
                max-width: 100%;
                min-height: 115px;
                max-height: 115px;
            }
            .btn_acao{
                margin-top: 20px;
                .btn{
                    border: 3px solid $color_destaque;
                    border-radius: 10px;
                    background-color: transparent;
                    display: block;
                    margin: 0;
                    max-width: max-content;
                    transition: .30s;
                    button{
                        display: block;
                        padding: 12px 25px;
                        text-transform: uppercase;
                        font-size: 14px;
                        font-size: 16px;
                        background-color: transparent;
                        color: #fff;
                        border: 0;
                        font-family: 'Montserrat Regular';
                        width: 250px;
                        cursor: pointer;
                    }
                    &:hover{
                        background-color: $color_destaque;
                    }      
                }
            }
            .resposta{
                width: 100%;
                display: block;
                padding: 10px 0px;
                font-size: 16px;
                margin-top: 20px;
            }
            .resposta.sucesso{
                border: 1px solid green;
                background-color: #95ff95;
                color: #050;
                font-family: 'Montserrat Bold';
            }
            .resposta.error{
                border: 1px solid red;
                background-color: #ffc5c5;
                color: #aa0202;
                font-family: 'Montserrat Bold';
            }
            @include desktop{
                input[type='text']{
                   width: 49%;
               }
           }
        }
    }
    @include desktop{
        .container{
            width: 100%;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .img{
                width: 37%;
                display: block;
                img{
                    max-width: 100%;
                    display: block;
                }
            }
            .form{
                width: 60%;
            }         
        }
    }
}