header{
    text-align: left;
    position: relative;
    z-index: 10;
    @include desktop{
        position: relative;
        z-index: 10;
        margin-bottom: -62px;
        text-align: center;
    }
    .container{
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    
        @include desktop{
            justify-content: center;
        }
        .header-top{
            display: inline-flex;
            flex-wrap: wrap;
            width: 100%;
            justify-content: center;
            padding: 16px 0px;

            .social{
                width: 100%;
                text-align: center;
                margin-bottom: 10px;
                a{
                    margin-right: 25px;
                    font-size: 17px;
                    transition: .30s;
                    &:last-child{
                        margin-right: 0px;
                    }
                    &:hover{
                        color: $color_destaque_hover;
                    }
                }
            }
            .contatos{
                width: 100%;
                text-align: center;
                a{
                    margin: 0px 0px 10px 0px;
                    font-size: 15px;
                    width: 100%;
                    display: block;
                    transition: .30s;
                    &:last-child{
                        margin-right: 0px;
                    }
                    &:hover{
                        color: $color_destaque_hover;
                    }
                }
            }
            @include tablet{
                justify-content: space-between;
                .social{
                    width: auto;
                    margin: 0;
                }
                .contatos{
                    width: auto;
                    display: inline-flex;
                    a{
                        width: auto;
                        margin: 0 15px 0 0;
                        &:last-child{
                            margin-right: 0px;
                        }
                    }
                }
            }
        }
        .header-logo{
                
            img{
                display: block;
                margin: 0 auto;
                max-width: 100%;
            }
        }
        .header-bottom{
            display: inline-flex;
            width: auto;
            flex-direction: row-reverse;
            justify-content: space-between;

            .bar{
                font-size: 35px;
                z-index: 2;
                cursor: pointer;
                
                &:hover{
                    i{
                        color: $color_destaque_hover;
                    }
                }
                i{
                    transition: .30s;
                    
                }
            }
            .menu{
                width: 100%;
                height: calc(100vh - 110px);
                position: absolute;
                top: 110px;
                left: -100%;
                transition: .30s;
                background-color: $color_destaque;
                overflow: auto;        
                padding: 20px 25px;        
                &.active{ 
                    left: 0;
                }
                ul{
                    li{
                        margin-bottom: 10px;

                        a{
                            transition: .30s;
                            font-size: 1em;
                            font-family: 'Montserrat Bold';
                        }
                        &:hover{
                            a{
                                color: $color_destaque_hover;
                            }
                        }
                    }
                }
                @include tablet{
                    top: 50px;
                }
            }
            @include desktop{
                width: 100%;
                justify-content: center;
                margin-top: 20px;

                .bar{
                    display: none;
                }
                .menu{
                    top: 0px;
                    left: 0px;
                    position: relative;
                    height: auto;
                    padding: 20px 0px;
                    max-width: 970px;
                    overflow: initial;
                   
                    ul{
                        width: 100%;
                        display: inline-flex;
                        justify-content: space-between;

                        li{
                            margin-bottom: 0px;
                        }
                    }

                    &::before{
                        content: ' ';
                        width: 75px;
                        height: 100%;
                        position: absolute;
                        left: -75px;
                        top: 0px;
                        background: rgb(7,24,153);
                        background: linear-gradient(220deg, rgba(7,24,153,1) 50%, rgba(255,255,255,0) 50%);
                    }
                    &:after{
                        content: ' ';
                        width: 75px;
                        height: 100%;
                        position: absolute;
                        right: -75px;
                        top: 0px;
                        background: rgb(7,24,153);
                        background: linear-gradient(135deg, rgba(7,24,153,1) 50%, rgba(255,255,255,0) 50%);
                    }
                }
            }
        }
    }
}

.admin{
    background-color: #fff;
}
.admin header{
    background-color: #000;
    position: relative;
    .container{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 10px;
        padding-bottom: 10px;
        .left{
            a{
                svg{
                    font-size: 35px;
                    cursor: pointer;
                    transition: .30s;
                    &:hover{
                        color: $color_destaque;
                    }
                }
            }
            @include desktop{
                visibility: hidden;
            }
        }
        .right{
            .menu{
                ul{
                    li{
                        display: inline-flex;
                        align-items: center;
                        position: relative;
                        
                        .name_user{
                            margin-right: 15px;
                        }
                        img{
                            width: 50px;
                            height: 50px;
                            border: 2px solid #fff;
                            border-radius: 50%;
                            overflow: hidden;
                        }
                       
                    }
                }
            }
        }
    }
    .menu_lateral{
        background-color: #393939;
        box-shadow: 0px 3px 30px -1px rgba(0,0,0,0.52);
        position: fixed;
        overflow-y: auto;
        flex-flow: column;
        min-height: initial;
        width: 250px;
        height: 100vh;
        min-height: 600px;
        left: -270px;
        top: 0;
        transition: .30s;
        padding-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        flex-direction: column;
        @include desktop{
            left: 0px;
        }
        &.active{
            left: 0px;
            .close{
                width: calc(100vw - 250px);
            }
        }
        .top{
            .logo{
                margin: 0 auto;
                display: none;
                @include tablet{
                    display: block;
                }
                @include desktop{
                    display: block;
                }
            }
            .menu{
                @include desktop{
                    display: none;
                }
                @include tablet{
                    display: none;
                }
                ul{
                    li{
                        display: inline-flex;
                        align-items: center;
                        position: relative;
                        
                        .name_user{
                            margin-left: 10px;
                        }
                        img{
                            width: 50px;
                            height: 50px;
                            border: 2px solid #fff;
                            border-radius: 50%;
                            overflow: hidden;
                            margin-left: 10px;
                        }
                        &:hover{
                            background-color: transparent;
                        }
                    }
                }
            }
            hr{
                margin: 20px 0px;
                color: #fff;
                display: block;
                
            }
            ul{
                text-align: left;
                .dash{
                    @include desktop{
                        display: none;
                    }
                }
                li{
                    transition: .30s;
                    
                    a{
                        padding: 8px 10px;
                        display: flex;
                        width: 100%;
                        
                        svg{
                            font-size: 20px;
                            margin-right: 10px;
                        }
                    }
                    .icon_svg, i{
                        width: 10%;
                        margin: 0 10px 0 0;
                        align-items: center;
                    }
                   
                    &:hover{
                        background-color: #000;
                    }
                    &.active{
                        background-color: #000;
                    }
                }
            }
        }
        
        .close{
            position: fixed;
            width: 0px;
            left: 250px;
            height: 100vh;
            top: 0;
            background-color: #0006;
            text-align: right;
            overflow: hidden;
            svg{
                font-size: 35px;
                padding: 5px 15px;
                width: 50px;
                height: 50px;
                cursor: pointer;
                transition: .30s;
                &:hover{
                    color: $color_destaque;
                }
            }
            @include desktop{
                display: none;
            }
        }
    }
}